<template>
  <div>
    <div>
      <div>
        <h1>Calculadora de Colas - Investigación Operativa II</h1>
      </div>
      <div>
        <p><b>Nombres: </b>Gary Limbert Apaza Mamani</p>
      </div>
      <div class="card">
        <div class="card-header">
          <h3 class="fw-bold text-center">
            COLA {{ setCanal }} POBLACIÓN {{ setPoblacion }}
          </h3>
        </div>
        <div class="card-body row">
          <div
            class="col-12 d-flex align-items-center justify-content-between fs-5 text-center mt-2"
          >
            (
            <span>M</span>
            /
            <span>M</span>
            /
            <span>S</span>
            :
            <span>FIFO</span>
            /
            <span>N</span>
            /
            <span>N</span>
            )
          </div>
          <div class="col-12 d-flex align-items-center fs-2 text-center">
            (
            <input type="number" class="form-control" min="0" v-model="lambda" placeholder="λ" />
            /
            <input type="number" class="form-control" min="0" v-model="mu" placeholder="µ" />
            /
            <input type="number" class="form-control" min="1" v-model="S" />
            :
            <input v-model="tipo" class="form-control" type="text" readonly />
            /
            <input
              type="number"
              class="form-control"
              min="0"
              v-model="N1"
              placeholder="∞"
              @change="changeN()"
            />
            /
            <input
              type="number"
              class="form-control"
              min="0"
              v-model="N2"
              placeholder="∞"
              @change="changeN()"
            />
            )
          </div>
          <button class="btn btn-success my-2" @click="resolver()">
            Resolver
          </button>

          <hr />

          <div>
            <h5 class="fw-bold">Resultados</h5>
            <p>δ = {{ delta }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "HomeView",
  data() {
    return {
      cola: "",
      poblacion: "",
      lambda: null,
      mu: null,
      S: 1,
      tipo: "FIFO",
      N1: null,
      N2: null,
      delta: null,
    };
  },
  computed: {
    setCanal() {
      if (this.S == 1) {
        return "MONOCANAL";
      } else {
        return "MULTICANAL";
      }
    },
    setPoblacion() {
      if (this.N1 && this.N2) {
        return "FINITA";
      } else {
        return "INFINITA";
      }
    },
  },
  methods: {
    changeN() {
      if (this.N1 == 0) { this.N1 = null }
      if (this.N2 == 0) { this.N2 = null }
    },
    resolver() {
      this.lambda == null
        ? this.$swal("λ vacio")
        : this.mu == null
        ? this.$swal("µ vacio")
        : (this.delta = this.lambda / (this.S * this.mu));
    },
  },
};
</script>